@charset "UTF-8";
@font-face {
  font-family: 'Gerbera';
  src: url("../fonts/Gerbera-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/Gerbera-Light.woff2") format("woff2"), url("../fonts/Gerbera-Light.woff") format("woff"), url("../fonts/Gerbera-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: 'Gerbera';
  src: url("../fonts/Gerbera-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Gerbera-Medium.woff2") format("woff2"), url("../fonts/Gerbera-Medium.woff") format("woff"), url("../fonts/Gerbera-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal; }

html,
body {
  background: #3a91f5;
  width: 100%;
  min-height: 100vh;
  padding: 0;
  margin: 0; }

body {
  font-family: "Gerbera", sans-serif;
  font-size: 16px;
  line-height: 1.3em;
  background-image: url(../img/background-bg.svg);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover; }
  @media screen and (min-width: 1200px) {
    body {
      background-position: 0 0; } }

img {
  max-width: 100%; }

h1 {
  font-family: "Gerbera", sans-serif;
  font-size: 44px;
  line-height: 1.3em;
  font-weight: 500;
  padding: 0;
  margin: 0; }

h3 {
  font-family: "Gerbera", sans-serif;
  font-size: 20px;
  line-height: 1.3em;
  font-weight: 500;
  padding: 0;
  margin: 0; }

.button {
  display: block;
  font-family: "Gerbera", sans-serif;
  font-size: 16px;
  line-height: 1;
  font-weight: 500;
  text-align: center;
  text-decoration: none;
  border-radius: 100px;
  padding: 17px;
  cursor: pointer;
  color: #fff;
  background: #f45544; }
  @media screen and (min-width: 1200px) {
    .button {
      padding: 17px 30px;
      display: inline-block; } }
  .button:hover {
    opacity: 0.95; }

.mobile-hidden {
  display: none; }
  @media screen and (min-width: 1200px) {
    .mobile-hidden {
      display: block; } }

.container {
  min-width: 280px;
  max-width: 1120px;
  margin: 0 auto;
  padding: 20px;
  display: grid; }
  @media screen and (min-width: 1200px) {
    .container {
      grid-template-areas: "header header header" "aside bubble ads";
      grid-template-columns: 23% 1fr 30%;
      grid-column-gap: 20px;
      padding: 0; } }

.header {
  grid-row: 3;
  padding: 20px 0 0 0; }
  @media screen and (min-width: 1200px) {
    .header {
      grid-area: header;
      grid-row: 1;
      padding: 60px 0 45px; } }
  .header__title {
    color: #fff;
    position: relative;
    font-size: 0;
    line-height: 0; }
    .header__title::before {
      content: "";
      display: block;
      background: url(../img/round-pic1.svg) 0 0 no-repeat;
      width: 275px;
      height: 151px; }
    @media screen and (min-width: 1200px) {
      .header__title {
        padding-right: 350px;
        font-size: 44px;
        line-height: 1.3em; }
        .header__title::before {
          position: absolute;
          top: -50px;
          right: 70px; }
        .header__title::after {
          content: "";
          display: block;
          background: url(../img/round-pic2.svg) 0 0 no-repeat;
          width: 84px;
          height: 84px;
          position: absolute;
          top: -70px;
          right: 0px; } }

.leftbar {
  color: #fff;
  font-weight: 500; }
  @media screen and (min-width: 1200px) {
    .leftbar {
      grid-area: aside; } }
  .leftbar__number {
    font-size: 36px;
    margin-right: 2px; }
  .leftbar__intro {
    margin: 0 0 20px 0; }
    @media screen and (min-width: 1200px) {
      .leftbar__intro {
        margin: 0 0 30px 0; } }
    .leftbar__intro_gap {
      padding-left: 20px; }
  .leftbar__top {
    display: grid;
    grid-template-columns: 75px 1fr; }
    @media screen and (min-width: 1200px) {
      .leftbar__top {
        display: block; } }
    .leftbar__top-scale {
      max-width: 85%; }
      @media screen and (min-width: 1200px) {
        .leftbar__top-scale {
          display: none; } }

@media screen and (min-width: 1200px) {
  .mainblock {
    grid-area: bubble; } }

.bubble {
  background: #fff;
  box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.1);
  border-radius: 24px;
  position: relative;
  font-weight: 500;
  min-height: 220px; }
  @media screen and (min-width: 1200px) {
    .bubble {
      margin: 0 17px 0 0px;
      min-height: 250px;
      border-radius: 34px; } }
  .bubble::after {
    display: table;
    clear: both; }
  .bubble__tail {
    width: 0;
    height: 0;
    border-left: 0px solid transparent;
    border-right: 18px solid transparent;
    border-top: 18px solid #fff;
    position: absolute;
    right: auto;
    left: 50px;
    bottom: -18px; }
    @media screen and (min-width: 1200px) {
      .bubble__tail {
        width: 0;
        height: 0;
        border-left: 18px solid transparent;
        border-right: 0px solid transparent;
        border-top: 18px solid #fff;
        left: auto;
        right: 50px; } }
  .bubble__image {
    float: right;
    margin: -10px -10px 10px 20px; }
  .bubble__title {
    letter-spacing: -0.5px;
    color: #a84fd4;
    margin: 5px 0 22px;
    animation: bubbleAnimation 2s infinite; }
  .bubble__name {
    display: grid;
    grid-template-columns: 23px 1fr;
    align-items: center;
    animation: bubbleAnimation 2s infinite; }
    .bubble__name-text {
      padding-left: 10px;
      letter-spacing: -0.3px;
      color: #0b276f;
      font-weight: 300; }
      @media screen and (min-width: 1200px) {
        .bubble__name-text {
          color: #272727;
          font-weight: 500; } }
  .bubble__intro {
    font-weight: 300;
    line-height: 1.4em;
    margin: 8px 0 0 0;
    color: #272727;
    animation: bubbleAnimation 2s infinite; }
    @media screen and (min-width: 1200px) {
      .bubble__intro {
        margin: 20px 0 0 0; } }
  .bubble__slide {
    position: absolute;
    top: 0;
    width: calc(100% - 56px);
    padding: 15px 18px 0;
    transition: opacity 0.2s ease-out;
    opacity: 0;
    overflow: hidden;
    height: 180px; }
    @media screen and (min-width: 1200px) {
      .bubble__slide {
        padding: 28px 28px 0;
        height: 185px; } }
    .bubble__slide_active {
      opacity: 1; }

.rightbar {
  color: #fff;
  font-weight: 500;
  position: relative;
  margin-top: 40px; }
  @media screen and (min-width: 1200px) {
    .rightbar {
      grid-area: ads;
      margin: 0; } }
  .rightbar::after {
    content: "";
    display: block;
    background: url(../img/round-pic3.svg) 0 0 no-repeat;
    width: 290px;
    height: 171px;
    margin-top: 35px; }
  .rightbar__info {
    margin: 0 0 20px 0; }
    @media screen and (min-width: 1200px) {
      .rightbar__info {
        margin: 20px 0; } }

/* keyframes для анимации бабла */
@keyframes bubbleAnimation {
  0%, 60%, 75%, 90%, to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  0% {
    opacity: 0;
    transform: translate3d(10px, 0, 0); }
  60% {
    opacity: 1;
    transform: translate3d(-5px, 0, 0); }
  75% {
    transform: translate3d(10px, 0, 0); }
  90% {
    transform: translate3d(-5px, 0, 0); }
  to {
    transform: translateZ(0); } }
